<template>
    <div class="container">
        <TopButton title="" />
        <div class="checkout-container">
            <!--   -->
            <div class="section">
                <h2 class="Shipping">  <span></span></h2>
                <button class="address">  </button>
                <h2 class="Shipping">  <span></span><span v-if="!deliveryRequest"></span></h2>
                <select v-model="deliveryRequest" class="dropdown">
                    <option disabled value=""> </option>
                    <option>  </option>
                    <option> </option>
                    <option> </option>
                </select>
            </div>

            <!--    -->
            <div class="section s1" v-if="cart.length > 0">
                <h2>  </h2>
                <div class="product-info" v-for="item in cart" :key="item.id">
                    <div class="product-image">
                        <img :src="item.image" alt=" " />
                    </div>
                    <div class="product-details">
                        <p>NEDE</p>
                        <p class="item_title">{{ item.title }}</p>
                        <p> : {{ item.quantity }}</p>
                        <p>
                            <strong>{{ item.price.toLocaleString() }}</strong>
                        </p>
                    </div>
                </div>
            </div>

            <!--  -->
            <div class="section s2">
                <h2></h2>
                <p class="small">  50%   .</p>
                <div class="points">
                    <input
                        type="number"
                        v-model.number="pointsUsed"
                        placeholder="0P"
                        class="input"
                        @input="validatePoints"
                    />
                    <button @click="useMaxPoints" class="btns"></button>
                    <button @click="resetPoints" class="btns"></button>
                </div>
            </div>

            <!--    -->
            <div class="section s3">
                <h2>  </h2>
                <p>
                      : <strong>{{ totalPrice.toLocaleString() }}</strong>
                </p>
                <p>
                     : <strong>- {{ pointsUsed.toLocaleString() }}</strong>
                </p>
                <p>
                    : <strong>{{ shippingFee.toLocaleString() }}</strong>
                </p>
                <p>
                    <strong>  : {{ finalPrice.toLocaleString() }}</strong>
                </p>
            </div>

            <!--   -->
            <div class="section s4">
                <h2> </h2>
                <div class="payment-methods">
                    <button class="btns bt">/</button>
                    <button class="btns bt"> </button>
                    <button class="btns bt"> </button>
                    <button class="btns bt"> </button>
                </div>
            </div>

            <!--   -->
            <section class="section s5">
                <div class="checkbox-group">
                    <div>
                        <input type="checkbox" v-model="agreeTerms" id="agree-terms" />
                        <label for="agree-terms"> </label>
                    </div>
                    <div>
                        <input type="checkbox" v-model="agreeOrderCheck" id="agree-order-check" />
                        <label for="agree-order-check">     <span></span></label>
                    </div>
                    <div>
                        <input type="checkbox" v-model="agreePrivacy" id="agree-privacy" />
                        <label for="agree-privacy">  3   <span></span></label>
                    </div>
                </div>
            </section>

            <!--   -->
            <div class="button-container">
                <button class="pay-button" @click="confirmOrder"></button>
            </div>
        </div>
        <div class="margin"></div>
    </div>
</template>

<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'Checkout',
    components: {
        TopButton
    },
    data() {
        return {
            deliveryRequest: '', //    
            pointsUsed: 0, //  
            shippingFee: 0, // 
            cart: [], //  
            agreeTerms: false, //  
            agreeOrderCheck: false, //   
            agreePrivacy: false //   
        };
    },
    created() {
        const cartQuery = this.$route.query.cart;
        if (cartQuery) {
            this.cart = JSON.parse(cartQuery); // JSON   
        }
    },
    computed: {
        totalPrice() {
            return this.cart.reduce((total, item) => total + item.price * item.quantity, 0);
        },
        finalPrice() {
            return Math.max(this.totalPrice - this.pointsUsed + this.shippingFee, 0);
        }
    },
    watch: {
        //        
        agreeTerms(newValue) {
            this.agreeOrderCheck = newValue;
            this.agreePrivacy = newValue;
        },
        //        
        agreeOrderCheck: 'updateAgreeTerms',
        agreePrivacy: 'updateAgreeTerms'
    },
    methods: {
        updateAgreeTerms() {
            this.agreeTerms = this.agreeOrderCheck && this.agreePrivacy;
        },
        useMaxPoints() {
            this.pointsUsed = Math.min(this.totalPrice * 0.5, this.totalPrice);
        },
        resetPoints() {
            this.pointsUsed = 0;
        },
        validatePoints() {
            if (this.pointsUsed < 0) this.pointsUsed = 0;
            if (this.pointsUsed > this.totalPrice * 0.5) {
                this.pointsUsed = Math.floor(this.totalPrice * 0.5);
            }
        },
        confirmOrder() {
            if (!this.deliveryRequest) {
                alert('  .');
                return;
            }
            if (!this.agreeOrderCheck || !this.agreePrivacy) {
                alert('    .');
                return;
            }
            alert(' ! .');
            this.$router.push({
                name: 'OrderComplete',
                query: { total: this.finalPrice }
            });
        }
    }
};
</script>

<style scoped>
.checkout-container {
    margin: 15px;
}

.section {
    margin-top: 30px;
}

.section.s1 {
    border-top: 1.5px solid #6f6f6f;
    border-bottom: 1.5px solid #6f6f6f;
    padding: 20px 0;
}

.section.s2,
.s3 {
    border-bottom: 1.5px solid #6f6f6f;
    padding-bottom: 20px;
}

.s4 {
    border-bottom: 1.5px solid #6f6f6f;
    padding-bottom: 30px;
}

.section h2 {
    font-size: 16px;
    font-weight: bold;
    letter-spacing: -0.5px;
    margin-bottom: 7px;
}

.small {
    font-size: 14px;
    margin-bottom: 3px;
}

.btns {
    display: inline-block;
    font-size: 13px;
    padding: 5px;
    margin-right: 5px;
    color: #6f6f6f;
    border: 0.7px solid #6f6f6f;
    background: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 7px;
    font-weight: bold;
}

.btns:hover {
    background: #555;
    color: #fff;
}

.btns.bt {
    width: 47%;
    padding: 10px 0;
    font-weight: bold;
}

.Shipping > span {
    font-size: 0.75rem;
    color: red;
}

.address {
    width: 100%;
    padding: 7px;
    margin-top: 3px;
    margin-bottom: 30px;
    background: none;
    border-radius: 5px;
    border: 1px solid #6f6f6f;
    font: 14px;
    color: #6f6f6f;
    font-weight: bold;
}
.dropdown {
    width: 100%;
    padding: 7px;
    border-radius: 5px;
    border: 1px solid #6f6f6f;
    font: 14px;
    color: #6f6f6f;
    font-weight: bold;
}

.product-info {
    display: flex;
    align-items: center;
}

.item_title {
    font-weight: bold;
    font-size: 15px;
}

.product-info > .product-details > p > strong {
    font-size: 1.15rem;
}

.product-image {
    width: 170px;
    height: 170px;
    margin-right: 10px;
}
.product-image img {
    width: 170px;
    height: 170px;
    object-fit: cover;
    margin-right: 10px;
}

.product-details p {
    margin: 5px 0;
}

.points {
    display: flex;
    align-items: center;
}

input.input {
    width: 60%;
}

.input {
    flex: 1;
    padding: 3px 0 3px 7px;
    margin-top: 7px;
    margin-right: 10px;
    border: 1px solid #6f6f6f;
    border-radius: 5px;
}

.checkbox {
    margin-top: 10px;
}

.checkbox > input {
    margin-right: 5px;
}

.checkbox > label {
    font-size: 0.9rem;
}

.checkbox-group > div > input {
    margin-right: 5px;
}

.checkbox-group > div > label {
    font-size: 0.9rem;
}
.checkbox-group > div > label span {
    font-size: 0.75rem;
    color: red;
}

.payment-methods .btn {
    margin: 5px;
    width: calc(50% - 10px);
}
.button-container {
    padding-bottom: 50px;
}
.pay-button {
    width: 100%;
    padding: 8px;
    margin-top: 15px;
    border: none;
    border-radius: 5px;
    background: #999;
    color: white;
    font-weight: bold;
}

.pay-button:hover {
    background: #555;
}

.margin {
    height: 25px;
}
</style>
