import axios from 'axios';

export default {
    methods: {
        // $api   HTTP  .
        // url:    URL
        // method: HTTP   (GET, POST, )
        // data:    (POST, PUT  )
        async $api(url, method, data) {
            return (
                // axios  HTTP  .
                (
                    await axios({
                        method: method, //   
                        url, //  URL 
                        data //   
                    }).catch((e) => {
                        //        .
                        console.log(e);
                    })
                ).data
            ); //    (data) .
        }
    }
};
