<template>
    <div class="container">
        <TopButton title="" />
        <div class="profile">
            <div class="userIcon">
                <img src="@/assets/img/user.svg" alt="user" />
            </div>
            <div class="title">
                <!--      -->
                <template v-if="Object.values(isLoggedIn).some((v) => v)"> {{ userName }} . </template>
                <template v-else>  . </template>
            </div>
            <!--      -->
            <div>
                <a class="login" v-if="Object.values(isLoggedIn).some((v) => v)" @click="toggleLogin">  > </a>
                <a class="login" v-else @click="Login">  > </a>
            </div>
        </div>
        <div class="stats">
            <div>
                <a class="link point">
                    <strong>0P</strong>
                    
                </a>
            </div>
            <div>
                <a class="link coupon">
                    <strong>0</strong>
                    
                </a>
            </div>
            <div>
                <a class="link order">
                    <strong>0</strong>
                    
                </a>
            </div>
            <div>
                <a class="link delivery">
                    <strong>0</strong>
                    
                </a>
            </div>
            <div>
                <a class="link evaluation">
                    <strong>0</strong>
                     
                </a>
            </div>
            <div>
                <a class="link inquiry">
                    <strong>0</strong>
                    
                </a>
            </div>
        </div>
        <div class="section">
            <ul class="event">
                
                <li>  / </li>
                <li>  </li>
            </ul>
            <ul class="service">
                
                <li> </li>
                <li> </li>
                <li>Q & A</li>
                <li>1:1 </li>
                <li></li>
            </ul>
        </div>
        <div class="weather"></div>
        <div class="weather-container">
            <div class="weatherBox" v-if="weather && weatherIcon">
                <div class="weatherBox-lt">
                    <div><img :src="weatherIcon" :alt="weatherIcon" /></div>
                    <div>
                        <p>{{ weather.temp }}</p>
                    </div>
                </div>
                <div class="weatherBox-rt">
                    <p>
                        {{ weather.temp_max }}  <br />
                        {{ weather.temp_min }} 
                    </p>
                    <p>
                         <span :style="{ color: airConditionColor }">{{ airCondition }}</span>
                    </p>
                </div>
            </div>
            <div class="weatherBox" v-else>
                <p>   ...</p>
            </div>
        </div>
    </div>
    <div class="magin"></div>
</template>
<script>
import TopButton from '@/components/TopButton.vue';
import { ref, onMounted } from 'vue';

export default {
    name: 'Mypage',
    components: {
        TopButton
    },
    data() {
        return {
            isLoggedIn: {
                google: false,
                kakao: false,
                naver: false,
                general: false
            },
            userName: '', //   
            naverAccessToken: null //    
        };
    },
    mounted() {
        console.log('Mounted: Checking initial login state...');
        this.updateIsLoggedIn(); // LocalStorage  
        this.checkNaverLogin(); //    
        this.checkLoginStatus(); //     
        console.log('Initial isLoggedIn:', this.isLoggedIn); //   

        // UI  
        setTimeout(() => {
            this.$forceUpdate(); // UI 
            console.log('Final isLoggedIn after timeout:', JSON.stringify(this.isLoggedIn));
        }, 500);
    },

    methods: {
        Login() {
            this.$router.push('/login');
        },

        updateIsLoggedIn() {
            this.isLoggedIn = {
                google: localStorage.getItem('googleLoggedIn') === 'true',
                kakao: localStorage.getItem('kakaoLoggedIn') === 'true',
                naver: localStorage.getItem('naverLoggedIn') === 'true',
                general: localStorage.getItem('isLoggedIn') === 'true' //    
            };
            console.log('Updated isLoggedIn:', JSON.stringify(this.isLoggedIn));
        },
        async logoutAll() {
            console.log('    ...');

            await Promise.all([this.googleLogout(), this.kakaoLogout(), this.naverLogout()]);

            try {
                console.log('PHP  ...');
                const response = await fetch('/api/auth/logout.php', {
                    method: 'POST',
                    credentials: 'include' //   
                });
                const data = await response.json();
                console.log('PHP Logout Response:', data);

                if (data.status !== 'success') {
                    throw new Error(data.message || 'PHP  ');
                }
            } catch (error) {
                console.error('PHP    :', error);
                alert('    .');
            } finally {
                this.isLoggedIn = { google: false, kakao: false, naver: false, general: false };
                this.userName = '';
                localStorage.clear();
                console.log('   .');
            }
        },

        googleLogout() {
            return new Promise((resolve) => {
                console.log('Logging out from Google...');
                const googleLogoutUrl = 'https://accounts.google.com/Logout';
                const iframe = document.createElement('iframe');
                iframe.style.display = 'none';
                iframe.src = googleLogoutUrl;
                document.body.appendChild(iframe);

                setTimeout(() => {
                    this.isLoggedIn.google = false;
                    localStorage.removeItem('googleLoggedIn');
                    console.log('Google logout complete.');
                    resolve();
                }, 500);
            });
        },

        kakaoLogout() {
            return new Promise((resolve) => {
                console.log('Logging out from Kakao...');
                if (window.Kakao && window.Kakao.Auth) {
                    window.Kakao.Auth.logout(() => {
                        this.isLoggedIn.kakao = false;
                        localStorage.removeItem('kakaoLoggedIn');
                        console.log('Kakao logout complete.');
                        resolve();
                    });
                } else {
                    console.error('Kakao SDK  .');
                    resolve();
                }
            });
        },

        naverLogout() {
            return new Promise((resolve) => {
                console.log('Logging out from Naver...');
                if (!this.naverAccessToken) {
                    console.log('Naver is not logged in.');
                    resolve();
                    return;
                }
                const logoutUrl = `https://nid.naver.com/oauth2.0/token?grant_type=delete&client_id=MyEYpin_VSCD218l6SHu&client_secret=kkCdziVPyr&access_token=${this.naverAccessToken}&service_provider=NAVER`;
                const iframe = document.createElement('iframe');
                iframe.style.display = 'none';
                iframe.src = logoutUrl;
                document.body.appendChild(iframe);

                setTimeout(() => {
                    this.naverAccessToken = null;
                    this.isLoggedIn.naver = false;
                    localStorage.removeItem('naverLoggedIn');
                    console.log('Naver logout complete.');
                    resolve();
                }, 500);
            });
        },

        async toggleLogin() {
            if (Object.values(this.isLoggedIn).some((v) => v)) {
                //     
                console.log('  ...');

                try {
                    await this.logoutAll(); //    
                    alert('.');

                    //     
                    this.isLoggedIn = { google: false, kakao: false, naver: false };
                    this.userName = '';
                    localStorage.clear();
                } catch (error) {
                    console.error('   :', error);
                    alert('    .');
                }
            } else {
                //    
                alert('  .');
                this.$router.push('/login');
            }
        },

        async checkLoginStatus() {
            try {
                const response = await fetch('/api/auth/check_login.php', {
                    method: 'GET',
                    credentials: 'include' //   
                });

                if (!response.ok) {
                    throw new Error(`Failed to check login status: ${response.status}`);
                }

                const data = await response.json();

                if (data.logged_in) {
                    this.isLoggedIn.general = true;
                    this.userName = data.username;
                    localStorage.setItem('isLoggedIn', 'true');
                } else {
                    this.isLoggedIn.general = false;
                    localStorage.removeItem('isLoggedIn');
                }
            } catch (error) {
                console.error('    :', error);
                this.isLoggedIn.general = false;
            } finally {
                this.updateIsLoggedIn(); // UI 
            }
        },

        checkNaverLogin() {
            const naverLogin = new window.naver.LoginWithNaverId({
                clientId: 'MyEYpin_VSCD218l6SHu',
                callbackUrl: 'http://green609b.dothome.co.kr/easylogin',
                isPopup: false
            });
            naverLogin.getLoginStatus((status) => {
                if (status) {
                    this.naverAccessToken = naverLogin.accessToken.accessToken;
                    localStorage.setItem('naverLoggedIn', 'true');
                    this.updateIsLoggedIn();
                } else {
                    console.log('No Naver login detected.');
                }
            });
        }
    },
    setup() {
        //   
        const weather = ref(null); //    
        const weatherIcon = ref(''); //   URL
        const airCondition = ref(''); //  
        const airConditionColor = ref(''); //     

        /*    */
        const fetchWeatherData = async () => {
            try {
                const weatherResponse = await fetch(
                    'https://api.openweathermap.org/data/2.5/weather?lat=37.2992437&lon=126.9782357&appid=822e1d81a695655b411c939d26499c1f&units=Metric'
                );
                const weatherData = await weatherResponse.json(); //   JSON 
                //   'weather' 
                weather.value = {
                    temp: weatherData.main.temp, //  
                    temp_min: weatherData.main.temp_min, //  
                    temp_max: weatherData.main.temp_max //  
                };

                //    
                const iconCode = weatherData.weather[0].icon;
                weatherIcon.value = `https://openweathermap.org/img/wn/${iconCode}@2x.png`;
            } catch (error) {
                console.error('     : ', error);
            }
        };

        /*     */
        const fetchAirQualityData = async () => {
            try {
                const airResponse = await fetch(
                    'http://api.openweathermap.org/data/2.5/air_pollution?lat=37.5070431&lon=126.8902185&appid=822e1d81a695655b411c939d26499c1f'
                );
                const airData = await airResponse.json();

                const fineDust = airData.list[0].components.pm2_5;

                if (fineDust <= 15) {
                    airCondition.value = '';
                    airConditionColor.value = '#0085FF';
                } else if (fineDust > 16 && fineDust <= 35) {
                    airCondition.value = '';
                    airConditionColor.value = '#009806';
                } else if (fineDust > 36 && fineDust <= 75) {
                    airCondition.value = '';
                    airConditionColor.value = '#FF2B1E';
                } else {
                    airCondition.value = ' ';
                    airConditionColor.value = '#B90B00';
                }
            } catch (error) {
                console.error('     : ', error);
            }
        };
        //     api
        onMounted(() => {
            //   
            fetchWeatherData();
            //   
            fetchAirQualityData();
        });

        //   
        return {
            weather,
            weatherIcon,
            airCondition,
            airConditionColor
        };
    }
};
</script>
<style scoped>
.container {
    text-align: center;
}

.profile {
    height: 100px;
    margin-top: 37px;
    line-height: 2rem;
}

.profile img {
    width: 85px;
    height: 85px;
    float: left;
    object-fit: contain;
    padding-top: 11px;
    border-radius: 50%;
    border: 1px solid #000;
    margin: 0 15px 0 10px;
}

.profile .title {
    margin-top: 13px;
    font-size: 1.3rem;
    font-weight: bold;
    color: #555;
    line-height: 30px;
    text-align: left;
}

.login {
    width: 60%;
    float: left;
    text-align: left;
    font-size: 1.1rem;
    text-decoration: none;
    color: #333;
    font-weight: bold;
    cursor: pointer;
}

.stats {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3px;
    margin: 10px 0 20px 0;
}

.stats div {
    padding: 5px;
    font-size: 0.9rem;
}

.link {
    cursor: pointer;
    text-decoration: none;
    color: #000;
}

.stats div:nth-child(3n-1) {
    border-left: 1px solid #000;
    border-right: 1px solid #000;
}
.stats div:nth-child(-n + 3) {
    border-bottom: 1px solid #000;
}

.stats div strong {
    display: block;
    font-size: 20px;
    margin-bottom: 5px;
}

.section {
    width: 100%;
    text-align: left;
}

.section ul {
    list-style: none;
    font: 0.9rem;
    font-weight: 700;
    color: #777;
    line-height: 2rem;
    margin-bottom: 20px;
    letter-spacing: -0.07rem;
    padding-left: 20px;
}

.section ul li {
    font-size: 1rem;
    color: #000;
}

/*  */

.weather {
    font: 0.9rem;
    font-weight: 700;
    color: #777;
    line-height: 2rem;
    letter-spacing: -0.07rem;
    padding-left: 20px;
    text-align: left;
    margin-top: 30px;
}

.weather-container {
    width: 100%;
    height: 70px;
    margin: 0 auto;
}

.weatherBox-lt div {
    display: block;
    float: left;
}

.weatherBox-lt img {
    float: left;
    width: 60px;
    margin-left: 10px;
}

.weatherBox-lt p {
    font-size: 1.3rem;
    line-height: 60px;
    font-weight: 700;
}

.weatherBox-rt {
    float: right;
    margin: 7px 10px;
}

.weatherBox-rt p {
    float: left;
    font-size: 0.9rem;
    text-align: left;
    font-weight: 600;
    margin-left: 7px;
}

.magin {
    height: 63px;
}
</style>
