<template>
    <div class="container">
        <TopButton title="" />
        <div class="check_box">
            <div class="check">
                <input type="checkbox" id="check" v-model="allSelected" @change="toggleAllSelected" />
                <label for="check">  </label>
            </div>
            <div class="checkdelete">
                <button @click="removeSelected"><span></span></button>
                <button @click="clearCart"><span></span></button>
            </div>
        </div>
        <div class="contents" v-if="cart.length > 0">
            <div class="contentBox" v-for="item in cart" :key="item.id">
                <div class="itemName">
                    <input type="checkbox" v-model="item.selected" @change="updateAllSelected" />
                    <label>{{ item.title }}</label>
                </div>
                <div class="itemBox">
                    <div class="itemImg">
                        <img :src="item.image" alt=" " />
                    </div>
                    <div class="itemOptions">
                        <p>{{ item.price.toLocaleString() }} </p>
                        <p>1   </p>
                        <div class="numBox">
                            <p></p>
                            <div class="item_qu">
                                <button @click="updateQuantity(item, item.quantity - 1)">-</button>
                                <span>{{ item.quantity }}</span>
                                <button @click="updateQuantity(item, item.quantity + 1)">+</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-else class="v-else">
            <p>  .</p>
        </div>
        <div class="bottomBox"></div>
        <div class="buyBox" v-if="cart.length > 0">
            <div class="buyContent">
                <p> {{ totalQuantity }} </p>
                <p>{{ totalPrice.toLocaleString() }} <span></span></p>
            </div>
            <div class="buyButton">
                <button @click="goToCheckout"></button>
            </div>
        </div>
    </div>
    <div class="margin"></div>
</template>

<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'ShoppingCart',
    components: {
        TopButton
    },
    data() {
        return {
            cart: [],
            allSelected: false //    //  
        };
    },
    created() {
        // localStorage   
        const storedCart = localStorage.getItem('cart');
        this.cart = storedCart ? JSON.parse(storedCart) : [];

        //   
        this.updateAllSelected();
    },
    computed: {
        //   
        totalPrice() {
            return this.cart.reduce((total, item) => total + item.price * item.quantity, 0);
        },
        //   
        totalQuantity() {
            return this.cart.reduce((total, item) => total + item.quantity, 0);
        }
    },
    methods: {
        saveCart() {
            // localStorage   
            localStorage.setItem('cart', JSON.stringify(this.cart));
        },
        addToCart(product) {
            //   
            const existingItem = this.cart.find((item) => item.id === product.id);
            if (existingItem) {
                existingItem.quantity++;
            } else {
                this.cart.push({ ...product, quantity: 1, selected: false });
            }
            this.saveCart(); //   
        },
        updateQuantity(item, newQuantity) {
            //  
            if (newQuantity < 1) return; //   
            item.quantity = newQuantity;
            this.saveCart(); //   
        },
        removeSelected() {
            //   
            this.cart = this.cart.filter((item) => !item.selected);
            this.saveCart(); //   
            this.updateAllSelected(); //   
        },
        clearCart() {
            //  
            this.cart = [];
            this.saveCart();
            this.updateAllSelected(); //   
        },
        goToCheckout() {
            //   
            this.$router.push({
                name: 'Checkout',
                query: { cart: JSON.stringify(this.cart) }
            });
        },
        toggleAllSelected() {
            //     
            this.cart.forEach((item) => {
                item.selected = this.allSelected;
            });
            this.saveCart(); //   
        },
        updateAllSelected() {
            //       
            this.allSelected = this.cart.every((item) => item.selected);
        }
    },
    watch: {
        cart: {
            handler() {
                this.updateAllSelected(); //       
            },
            deep: true
        }
    }
};
</script>
<style scoped>
/*   */
.check_box {
    margin-top: 25px;
    display: flex;
    justify-content: space-between;
    font-size: 12px;
}

.check {
    margin-left: 15px;
}
.check input {
    margin-right: 5px;
}

.checkdelete button {
    border: none;
    background: none;
    margin-right: 10px;
    padding: 0;
}

/*   */
.contents {
    margin: 0 auto;
    width: 95%;
    height: 100vh;
    margin-top: 15px;
    border: 1px solid #999;
    border-radius: 10px 10px 0 0;
}

.itemName {
    font-weight: bold;
    font-size: 1rem;
    letter-spacing: -1px;
    margin-bottom: 20px;
}

.itemName input {
    margin-right: 5px;
}

.contents .contentBox {
    margin: 0 auto;
    width: 90%;
    height: 200px;
    margin-top: 25px;
    border-bottom: 1px solid #999;
}

.contents .contentBox .itemBox {
    width: 100%;
    margin: 15px 0;
}
.contents .contentBox .itemBox .itemImg {
    width: 137px;
    height: 137px;
    background-color: #999;
    margin-right: 20px;
    float: left;
}
.contents .contentBox .itemBox .itemImg img {
    width: 137px;
    height: 137px;
}
.itemOptions {
    text-align: left;
    line-height: 1.3rem;
}
.itemOptions p {
    margin: 5px;
    margin-bottom: 7px;
    text-align: left;
}

.itemOptions p:first-child {
    font-weight: bold;
    font-size: 1.2rem;
}

.itemOptions .numBox {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 140px;
    margin-top: 10px;
    padding: 3px 2px;
    background: #ececec;
    text-align: left;
    border-radius: 3px;
}

.itemOptions .numBox p {
    font-size: 0.95rem;
}

.numBox button {
    width: auto;
    font-size: 1.1rem;
    margin: 3px 0;
    border: none;
    background: none;
}

.item_qu {
    float: right;
}

.itemOptions span {
    font-weight: bold;
    font-size: 1.1rem;
}

/*   */
.buyBox {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #ccc;
    height: 200px;
    border-radius: 45px 45px 0 0;
}
.buyBox .buyContent {
    width: 80%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    margin-top: 30px;
}

.buyBox .buyContent p {
    float: left;
    font-size: 1.2rem;
    font-weight: bold;
    margin: 0 7px;
}

.buyButton {
    width: 90%;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    margin-top: 15px;
}

.buyButton button {
    height: 40px;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 5px;
    font-weight: bold;
}
.v-else {
    height: 100vh;
    text-align: center;
    line-height: 75vh;
}

.v-else > p {
    margin: 0 auto;
    font-size: 1.25rem;
    letter-spacing: -0.3px;
    font-weight: bold;
}

.margin {
    height: 150px;
}
</style>
