<template>
    <div class="container">
        <TopButton title=" " />

        <div class="map-container">
            <div id="map"></div>
        </div>
        <div class="company">
            <p><span></span> :    97, / 6</p>
            <p><span></span> : B</p>
            <p><span></span> : 02-1234-5678</p>
            <p><span> </span> : 010-1234-5678</p>
            <p><span></span> : example@company.com</p>
        </div>
    </div>
    <div class="margin"></div>
</template>
<script>
import { onMounted } from 'vue';
import TopButton from '@/components/TopButton.vue';
export default {
    name: 'CompanyProfile',
    components: { TopButton },
    setup() {
        onMounted(() => {
            //   
            const script = document.createElement('script');
            script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=c9c204b182c8f81f46c49322fed61963&autoload=false`;
            script.onload = () => {
                window.kakao.maps.load(() => {
                    //  
                    const container = document.getElementById('map'); //   div
                    const options = {
                        center: new kakao.maps.LatLng(37.5070431, 126.8902185), //    ()
                        level: 4 //  
                    };
                    const map = new kakao.maps.Map(container, options);

                    //  
                    const markerPosition = new kakao.maps.LatLng(37.5070431, 126.8902185); //  
                    const marker = new kakao.maps.Marker({
                        position: markerPosition
                    });
                    marker.setMap(map);

                    //   
                    const infowindow = new kakao.maps.InfoWindow({
                        content: '<div class="marker"> </div>' //   
                    });
                    infowindow.open(map, marker);
                });
            };
            document.head.appendChild(script);
        });
    }
};
</script>
<style> /* marker css    */
.map-container {
    width: 100%;
    margin: 0 auto;
    padding-top: 20px;
}
#map {
    width: 350px;
    height: 280px;
    margin: 0 auto;
    background: #999;
}
.marker {
    padding: 5px;
    width: 150px;
    text-align: center;
    font-weight: 600;
}

.company {
    font-size: 0.95rem;
    margin-left: 10px;
    margin-top: 20px;
}

.company p {
    margin-bottom: 0.9rem;
}

.company > p span {
    font-weight: bold;
}

.margin {
    height: 63px;
}
</style>
