<template>
    <div class="back">
        <button class="back_btn" @click="goBack" style="background-color: #fff">
            <img src="../assets/img/chevron-left-solid.svg" alt="" width="20" height="20" />
        </button>
    </div>
    <div class="logo">
        <img src="../assets/img/nede_3.jpg" alt="NEDE logo" />
    </div>
    <div class="login-container">
        <form @submit.prevent="handleLogin">
            <input type="text" v-model="form.username" placeholder="" required />
            <input
                type="password"
                v-model="form.password"
                placeholder="(8~10, ++)"
                required
            />
            <div class="options">
                <label> <input type="checkbox" v-model="form.autoLogin" />  </label>
                <label> <input type="checkbox" v-model="form.saveId" />   </label>
            </div>
            <div class="login_easy">
                <button class="login_bg" type="submit" :disabled="loading"></button>
                <button class="login_bg" type="button" @click="Easylogin"> </button>
            </div>
        </form>

        <div class="links">
            <button @click="navigateTo('/register')"></button>
            <button @click="navigateTo('#')"> </button>
            <button @click="navigateTo('#')"> </button>
        </div>

        <footer>
            <div><span>ver.1.1.1 </span></div>
            <div>
                <span>  </span>
                <span></span>
            </div>
        </footer>
    </div>
    <div class="margin"></div>
</template>

<script>
export default {
    data() {
        return {
            form: {
                username: '',
                password: '',
                autoLogin: false,
                saveId: false
            },
            loading: false,
            message: ''
        };
    },
    methods: {
        async handleLogin() {
            if (!this.form.username || !this.form.password) {
                alert('  .');
                return;
            }

            this.loading = true;
            try {
                const response = await fetch('http://green609b.dothome.co.kr/api/auth/login.php', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    credentials: 'include',
                    body: JSON.stringify(this.form)
                });

                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                const result = await response.json(); // JSON 
                console.log('Response result:', result); //   

                if (result.status === 'success') {
                    this.isLoggedIn = true; //   
                    localStorage.setItem('isLoggedIn', 'true'); //   localStorage 
                    //     
                    alert(' .');
                    this.$router.push('/mypage');
                    /* this.$router.push('/'); */
                    this.username = ''; //   
                    this.password = '';
                } else {
                    //     
                    this.error = result.data?.message || 'Login failed';
                }
            } catch (error) {
                console.error('Login error:', error); //   
                this.error = error.message || 'An unexpected error occurred';
            } finally {
                this.loading = false;
            }
        },
        Easylogin() {
            this.$router.push('/easylogin');
        },
        navigateTo(path) {
            this.$router.push(path);
        },
        goBack() {
            this.$router.go(-1); // 
        }
    }
};
</script>

<style scoped>
.back button {
    margin: 18px 0 0 13px;
}

.login-container {
    max-width: 350px;
    margin: 0 auto;
}
.logo {
    width: 350px;
    margin: 0 auto;
    margin-bottom: 60px;
}

.logo img {
    display: block;
    margin: 0 auto;
    margin-top: 50px;
}

form {
    display: flex;
    flex-direction: column;
}

input,
button {
    margin-bottom: 10px;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

button {
    background-color: #333;
    color: white;
    border: none;
    cursor: pointer;
}

button:disabled {
    background-color: #aaa;
    cursor: not-allowed;
}

.options {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.links {
    display: flex;
    justify-content: space-between;
    margin-top: 12px;
}

.links button {
    background-color: #acacac;
    width: 110px;
    font-size: 0.9em;
}

.links button:hover {
    background-color: #777;
}

.login_easy {
    margin-top: 10px;
}

.login_bg {
    width: 100%;
    background-color: #acacac;
}

.login_bg:hover {
    background-color: #777;
}

footer {
    margin-top: 20px;
    font-size: 12px;
    display: flex;
    justify-content: space-between;
    color: #555;
    border-top: 1px solid #adadad;
    padding-top: 15px;
}
.margin {
    height: 67px;
}
</style>
