<template>
    <div class="container">
        <div>
            <TopButton :title="isEditMode ? ' ' : ' '" />
            <form @submit.prevent="handleSubmit" enctype="multipart/form-data">
                <div>
                    <label for="title"></label>:
                    <input v-model="title" id="title" placeholder=" " required />
                </div>
                <div>
                    <label for="author"></label>:
                    <input v-model="author" id="author" placeholder=" " readonly />
                </div>
                <div>
                    <label for="image"> </label>:
                    <input type="file" id="image" @change="handleFileUpload" accept="image/*" />
                </div>
                <div>
                    <label for="content"></label>
                    <textarea v-model="content" id="content" placeholder="  " required></textarea>
                </div>
                <button type="submit">{{ isEditMode ? ' ' : ' ' }}</button>
            </form>
        </div>
    </div>
    <div class="margin"></div>
</template>

<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'ReviewForm',
    components: {
        TopButton
    },
    data() {
        return {
            author: '', //  
            title: '', //  
            content: '', //  
            image: null, //   
            isEditMode: false, //   
            reviewId: null //   ID
        };
    },
    methods: {
        //       
        async checkLoginStatus() {
            try {
                const response = await fetch('http://green609b.dothome.co.kr/api/auth/check_login.php', {
                    method: 'GET',
                    credentials: 'include' //   
                });
                const result = await response.json();

                if (result.logged_in) {
                    this.author = result.username; //   ID 
                } else {
                    alert('  .   .');
                    this.$router.push('/login');
                }
            } catch (error) {
                console.error('     :', error);
            }
        },
        //   
        handleFileUpload(event) {
            this.image = event.target.files[0];
        },
        //    
        async fetchReviewData() {
            try {
                const response = await fetch(
                    `http://green609b.dothome.co.kr/api/posts/fetch_review.php?id=${this.reviewId}`
                );
                if (!response.ok) {
                    throw new Error(`    .  : ${response.status}`);
                }
                const review = await response.json();
                this.title = review.title;
                this.content = review.content;
                this.image = null; //      
            } catch (error) {
                console.error('     :', error);
                alert('    .');
            }
        },
        //    
        async handleSubmit() {
            const formData = new FormData();
            formData.append('title', this.title);
            formData.append('content', this.content);
            if (this.image) {
                formData.append('image', this.image);
            }

            const apiUrl = this.isEditMode
                ? `http://green609b.dothome.co.kr/api/posts/update_review.php?id=${this.reviewId}`
                : 'http://green609b.dothome.co.kr/api/posts/create_review.php';

            try {
                const response = await fetch(apiUrl, {
                    method: 'POST',
                    body: formData,
                    credentials: 'include'
                });
                const result = await response.json();
                alert(result.message);

                if (result.status === 'success') {
                    this.$router.push('/reviews'); //    
                }
            } catch (error) {
                console.error('    :', error);
            }
        }
    },
    mounted() {
        this.checkLoginStatus();

        if (this.$route.params.id) {
            this.isEditMode = true;
            this.reviewId = this.$route.params.id; //   ID 
            this.fetchReviewData(); //    
        }
    }
};
</script>

<style scoped>
form {
    width: 90%;
    height: 83vh;
    max-width: 500px;
    margin: 0 auto; /*   */
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #fdfdfd;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    text-align: left;
}

input {
    border: none;
    background: none;
}

/*   */
label {
    width: 105px;
    display: inline-block;
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 0.95rem;
    line-height: 1.8rem;
    color: #333;
}

input#image {
    width: 60%;
    border: none;
}

/*    */
input[type='text'],
input[type='file'],
textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 0.85rem;
    color: #555;
}

input {
    font-size: 0.85rem;
}
/*     */
input[type='text']:focus,
textarea:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/*     */
textarea {
    height: 56vh;
    resize: vertical;
}

/*   */
button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #999;
    color: white;
    border: none;
    border-radius: 7px;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
}

button:hover {
    background-color: #666;
}

/*     */
input[type='file'] {
    padding: 3px;
}

.margin {
    height: 63px;
}
</style>
