<template>
    <div class="container">
        <TopButton title="" />
        <div class="order-complete-container">
            <div class="order">
                <div class="order_img">
                    <img src="../assets/img/OrderComplete.jpg" alt="oreder" />
                    <p> : 3548798754684241</p>
                </div>
                <div class="complete">
                    <p>
                          : <strong>{{ totalAmount.toLocaleString() }} </strong>
                    </p>
                </div>
                <button class="btns"></button>
                <button @click="goToMain" class="btns"> </button>
            </div>
        </div>
    </div>
</template>
<script>
import TopButton from '@/components/TopButton.vue';
export default {
    name: 'OrderComplete',
    components: { TopButton },
    data() {
        return {
            totalAmount: 0 //    
        };
    },
    created() {
        // query     
        const totalQuery = this.$route.query.total;
        if (totalQuery && !isNaN(totalQuery)) {
            this.totalAmount = parseInt(totalQuery, 10); //   
        } else {
            console.warn('    .');
            this.totalAmount = 0; //  
        }
    },
    methods: {
        goToMain() {
            this.$router.push('/'); //   
        }
    }
};
</script>
<style scoped>
.order-complete-container {
    height: 39vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: 600px;
    font-family: Arial, sans-serif;
    margin: 0 auto;
}

.order_img {
    width: 100%;
    margin: 200px auto 0 auto;
}

.order_img img {
    width: 300px;
}

.order_img p {
    margin-top: -60px;
    font-size: 1.2rem;
}

p {
    margin-top: 0px;
    line-height: 1.8rem;
    font-size: 1.2rem;
}

.complete {
    margin-top: 15px;
    font-size: 1.1rem;
}

.btns {
    background-color: #acacac;
    border: none;
    border-radius: 20px;
    margin: 0px 5px;
    padding: 7px 15px;
    cursor: pointer;
    margin-top: 7px;
    font-size: 1rem;
    color: white;
    font-weight: bold;
}
</style>
