<template>
    <div class="container">
        <TopButton title="" />
        <p class="title">{{ review.title }}</p>
        <div v-if="isLoading">  ...</div>
        <div v-else class="review-container">
            <div class="reviewImg">
                <img :src="review.image_path" alt="" v-if="review.image_path" />
            </div>
            <div class="review-content">
                <p class="id">{{ review.username }}</p>
                <p class="score"><img src="@/assets/img/leaf.svg" alt="score" /> 4.3</p>
                <p class="content">{{ review.content }}</p>
            </div>
            <div class="review-bottom">
                <div>
                    <button type="button" @click="ReviewList" class="bottom-Btn">
                        <img src="../assets/img/chevron-left-solid.svg" alt="" width="15" height="15" />
                         
                    </button>
                </div>
                <div class="buttons" v-if="isOwner">
                    <button type="button" @click="navigateToEdit"></button><button
                        type="button"
                        @click="deleteReview"
                    >
                        
                    </button>
                </div>
            </div>
        </div>
    </div>
    <div class="margin"></div>
</template>

<script>
import TopButton from '@/components/TopButton.vue';
import ReviewList from './ReviewList.vue';
export default {
    name: 'ReviewPage',
    components: { TopButton },
    data() {
        return {
            review: {}, //   
            loggedInUser: null, //   ID
            isOwner: false, //   
            isLoading: true //  
        };
    },
    methods: {
        //   
        async fetchReview() {
            this.isLoading = true;
            const reviewId = this.$route.params.id; // URL   ID 
            try {
                const response = await fetch(
                    `http://green609b.dothome.co.kr/api/posts/fetch_review.php?id=${reviewId}`
                );
                if (!response.ok) {
                    throw new Error(`   .  : ${response.status}`);
                }
                const data = await response.json();
                this.review = data;

                //    
                this.isOwner = this.review.username === this.loggedInUser;
            } catch (error) {
                console.error('     :', error);
                alert('   .  .');
            } finally {
                this.isLoading = false;
            }
        },
        //    
        async fetchLoggedInUser() {
            try {
                const response = await fetch('http://green609b.dothome.co.kr/api/auth/check_login.php', {
                    method: 'GET',
                    credentials: 'include' //   
                });
                const result = await response.json();
                if (result.logged_in) {
                    this.loggedInUser = result.username; //   ID 
                    console.log('Logged-in user ID:', this.loggedInUser); //  
                } else {
                    this.loggedInUser = null;
                }
            } catch (error) {
                console.error('      :', error);
            }
        },
        //   
        navigateToEdit() {
            this.$router.push(`/reviews/edit/${this.review.user_id}`); //   
        },
        //  
        async deleteReview() {
            if (confirm(' ?')) {
                try {
                    const response = await fetch(`http://green609b.dothome.co.kr/api/posts/delete_review.php`, {
                        method: 'POST',
                        headers: { 'Content-Type': 'application/json' },
                        body: JSON.stringify({ id: this.review.user_id })
                    });
                    const result = await response.json();
                    alert(result.message);
                    if (result.status === 'success') {
                        this.$router.push('/reviews'); //     
                    }
                } catch (error) {
                    console.error('    :', error);
                }
            }
        },
        ReviewList() {
            this.$router.push('/reviews');
        }
    },
    async mounted() {
        await this.fetchLoggedInUser(); //    
        await this.fetchReview(); //   
    }
};
</script>

<style scoped>
.title {
    width: 90%;
    margin: 0 auto;
    margin-top: 30px;
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
}

.reviewImg img {
    height: auto;
    object-fit: scale-down;
}

.reviewImg {
    background-color: #fff;
    margin: 0 auto;
    margin-top: 15px;
    width: 90%;
}
.reviewImg img {
    display: block;
    width: 100%;
    height: 500px;
}
.review-container {
    margin: 0 auto;
    width: 90%;
}
.review-content {
    margin: 0 auto;
    margin-top: 20px;
    width: 100%;
    margin-bottom: 10px;
}

.id {
    float: right;
    font-size: 0.9rem;
}

.score {
    font-size: 1.05rem;
}

.score img {
    margin-right: 3px;
    filter: invert(10%) sepia(89%) saturate(6058%) hue-rotate(83deg) brightness(108%) contrast(86%);
}

.content {
    width: 100%;
    height: 140px;
    text-align: justify;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    margin-bottom: 10px;
    padding: 12px 0;
    overflow-y: auto;
}
.review-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0;
}
.bottom-Btn {
    border: none;
    background-color: #fff;
    font-size: 0.9em;
    display: flex;
    align-items: center;
    letter-spacing: -0.5px;
}
.buttons button {
    border: none;
    background-color: #fff;
    box-sizing: border-box;
    font-size: 0.9em;
}
.buttons button:hover {
    font-weight: bold;
}
.bottom-Btn:hover {
    font-weight: bold;
}
.margin {
    height: 63px;
}
</style>
